.header {
  display: flex;
  justify-content: space-between;
  background: rgba(79, 39, 191, 1);
  font-family: "Montserrat";
  font-size: 20px;
  color: white;
  height: 80px;
  width: auto;

  .header-leftSide {
    margin-left: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    .logo {
      padding-right: 15px;
      img {
        height: 40px;
      }
    }
  }

  .header-rightSide {
    margin-right: 25px;
    display: flex;
    justify-content: center;
    align-items: center;

    .logo {
      padding-right: 15px;
      img {
        height: 40px;
      }
    }
  }
}

@media only screen and (max-width: 430px) {
  .header {
    font-size: 15px;
  }
}
